<template>
  <div class="container">
    <h1>{{ $t('register.title') }}</h1>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <form>
          <div class="form-group text-left">
            <label for="username">{{ $t('register.username') }}:</label>
            <input type="text" class="form-control" id="username" placeholder="User Name" v-model="username" />
          </div>
          <div class="form-group text-left">
            <label for="password">{{ $t('register.password') }}:</label>
            <input type="password" class="form-control" id="password" v-model="password" v-on:input="changePassword" />
          </div>
          <div class="form-group text-left">
            <label for="password">{{ $t('register.repassword') }}:</label>
            <input type="password" class="form-control" id="repassword" v-model="repassword" v-on:input="changePassword" />
          </div>
        </form>
        <button class="btn btn-primary" v-on:click="register">{{ $t('register.title') }}</button>
      </div>
    </div>
    <br/>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <button class="btn btn-success" v-on:click="back">{{$t('register.back')}}</button>
      </div>
    </div>
    <br/>
    <div class="row" v-show="showTip">
      <div class="col-sm-4 offset-sm-4 text-center">
        <p class="alert alert-danger">{{$t('register.tip')}}</p>
      </div>
    </div>
    <select class="locale" v-model="locale" v-on:change="changeLocale">
      <option value="zh">中文</option>
      <option value="en">English</option>
    </select>
  </div>
</template>

<script>
export default {
  name: 'RegisterPage',
  data () {
    return {
      username: '',
      password: '',
      repassword: '',
      showTip: false,
      locale: localStorage.getItem('locale')
    }
  },
  created () {
    this.locale = localStorage.getItem('locale')
    if (this.locale == null) {
      this.locale = 'en'
      localStorage.setItem('locale', this.locale)
    }
    this.$i18n.locale = this.locale
  },
  methods: {
    register: function () {
      this.username = this.username.replace(/(^\s*)|(\s*$)/g, '')
      this.password = this.password.replace(/(^\s*)|(\s*$)/g, '')
      this.repassword = this.repassword.replace(/(^\s*)|(\s*$)/g, '')
      console.log(this.username + '|')
      console.log(this.password + '|')
      console.log(this.repassword + '|')
      if (!this.isEmpty(this.username) && this.isEmpty(this.password) && this.password === this.repassword) {
        // send register request to server
      }
    },
    changeLocale: function () {
      console.log(this.locale)
      localStorage.setItem('locale', this.locale)
      this.$i18n.locale = this.locale
    },
    back: function () {
      this.$router.push('/')
    },
    changePassword: function () {
      if (this.password != this.repassword) {
        this.showTip = true
      } else {
        this.showTip = false
      }
    },
    isEmpty: function (str) {
      if (typeof str == 'undefined' || str == null || str.lenght < 1 || str == '') {
        return true
      }
      return false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  /* color: #42b983; */
}
.locale {
  position: fixed;
  right: 2%;
  top: 2%;
}
</style>



