<template>
  <div>
    <div class="page-container">
      <!--/content-inner-->
      <div class="left-content">
        <!-- top panel -->
        <div class="mother-grid-inner">
          <!--header start here-->
          <div class="header-main">
            <div class="w3layouts-left">
              <!--search-box-->
              <div class="w3-search-box">
                <div class="row">
                  <input type="text" id="keyword" placeholder="Search..." required="">
                  <button v-on:click="search"><i class="fa fa-search"></i></button>
                </div>
              </div>
              <!--//end-search-box-->
              <div class="clearfix"></div>
            </div>
            <div class="profile_details w3l">
              <ul>
                <li class="dropdown profile_details_drop">
                  <a class="dropdown-toggle" data-toggle="dropdown" aria-expanded="false">
                    <div class="profile_img">  
                      <span class="prfil-img"><img v-bind:src="profileInfo.iconSource" alt=""> </span> 
                      <div class="user-name">
                        <p>{{ profileInfo.userName }}</p>
                      </div>
                      <i class="fa fa-angle-down"></i>
                      <i class="fa fa-angle-up"></i>
                      <div class="clearfix"></div>
                    </div>  
                  </a>
                  <ul class="dropdown-menu drp-mnu">
                    <li v-on:click="testAPI"> <a><i class="fa fa-cog"></i> Settings</a> </li>
                    <li> <a><i class="fa fa-user"></i> Profile</a> </li>
                    <li> <a v-on:click="logout"><i class="fa fa-sign-out"></i> Logout</a> </li>
                  </ul>
                </li>
              </ul>
              <div class="clearfix"> </div>
            </div>
            <div class="clearfix"> </div>
          </div>
        </div>
        <!-- content -->
        <div class="agile-info-stat" v-if="0 == active">
          <div class="stats-info stats-last widget-shadow">
            <table class="table stats-table">
              <thead>
                <tr>
                  <th>{{ 'NO.' }}</th>
                  <th>{{ $t('myarchive.title') }}</th>
                  <th>STATUS</th>
                  <th>{{ $t('myarchive.created') }}</th>
                </tr>
              </thead>
              <tbody v-for="(archive, index) in archives" v-bind:key="archive.title">
                <tr v-on:click="previewArchive(index)">
                  <th scope="row">{{ index + 1 }}</th>
                  <td>{{ archive.title }}</td>
                  <td><span class="label label-success">Accessable</span></td>
                  <td><h5>{{ archive.created }} <i class="fa fa-level-up"></i></h5></td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="clearfix"></div>
          <!-- archive preview panel -->
          <div class="archive-preview well">
            <h3>{{ curTitle }}</h3>
            <p class="col-sm-9 text-left" style="line-height: 2rem; overflow: hidden; text-overflow: ellipsis;">{{ curAbstract }}</p>
            <img v-bind:src="curPreviewSrc" style="max-height: 20em; height: 100%;" />
            <div class="clearfix"></div>
          </div>
          <div class="clearfix"></div>
          <!-- //archive preview panel -->
        </div>
        <!--four-grids here-->
        <div class="four-grids" v-if="1 == active">
          <div v-if="1 == active" v-for="info in accountInfo" v-bind:key="info.name">
            <div class="col-md-3 four-grid">
              <div class="four-agileits" v-bind:style="{ background: info.background }">
                <div class="icon">
                  <i v-bind:class="info.icon" aria-hidden="true"></i>
                </div>
                <div class="four-text">
                  <h3>{{ info.name }}</h3>
                  <h4> {{ info.value }} </h4>
                </div>
              </div>
            </div>
          </div>
          <div class="clearfix"></div>
        </div>
        <!--//four-grids here-->
        <!-- input form -->
        <div class="grid-form1" v-if="2 == active">
          <div class="form-group">
            <label for="inputFile">{{ $t('uploadfile.title') }}</label>
            <div class="row">
              <input type="file" id="inputFile" class="offset-sm-3" v-on:change="selectFile($event, 0)" />
              <img id="inputPreview" style="max-height: 10em; height: 100%;" />
            </div>
            <div class="clearfix"></div>
            <p class="help-block">{{ $t('uploadfile.tip') }}</p>
          </div>
        </div>
        <!-- //input form -->
        <!-- search result -->
        <div class="search-result" v-if="3 == active" v-for="r in searchResults" v-bind:key="r.title">
          <div class="well">
            <h3>{{r.title}}</h3>
            <p class="text-left" style="overflow: hidden; text-overflow: ellipsis; white-space: nowrap;">{{r.abstract}}</p>
          </div>
          <div class="clearfix"></div>
        </div>
        <!-- //search result -->
      </div>
      <!--//content-inner-->
      <!--/sidebar-menu-->
      <div class="sidebar-menu">
        <header class="logo1">
          <a class="sidebar-icon" v-on:click="toggleSidebar"> <span class="fa fa-bars"></span> </a> 
        </header>
        <div style="border-top:1px ridge rgba(255, 255, 255, 0.15)"></div>
        <div class="menu">
          <ul id="menu" v-for="(item, index) in items" v-bind:key="item.name">
            <li v-if="index == active" v-on:click="changeTab(index)" class="active"><a href="javascript:void(0)"><i v-bind:class="item.icon"></i> <span>{{ item.name }}</span><div class="clearfix"></div></a></li>
            <li v-else v-on:click="changeTab(index)"><a href="javascript:void(0)"><i v-bind:class="item.icon"></i> <span>{{ item.name }}</span><div class="clearfix"></div></a></li>
          </ul>
        </div>
      </div>
      <div class="clearfix"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'IndexPage',
  data () {
    return {
      profileInfo: {
        iconSource: require('@/assets/mountain.jpg'),
        userName: 'Tom'
      },
      items: [],
      toggle: true,
      archives: [
        {
          title: 'First Archive',
          created: '2018-07-05',
          abstract: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.',
          imgsrc: require('@/assets/logo.png')
        }, {
          title: 'Second Archive',
          created: '2018-07-06',
          abstract: 'Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo. Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem sequi nesciunt. Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore et dolore magnam aliquam quaerat voluptatem. Ut enim ad minima veniam, quis nostrum exercitationem ullam corporis suscipit laboriosam, nisi ut aliquid ex ea commodi consequatur? Quis autem vel eum iure reprehenderit qui in ea voluptate velit esse quam nihil molestiae consequatur, vel illum qui dolorem eum fugiat quo voluptas nulla pariatur?',
          imgsrc: require('@/assets/mountain.jpg')
        }, {
          title: 'Third Archive',
          created: '2018-07-06',
          abstract: 'But I must explain to you how all this mistaken idea of denouncing pleasure and praising pain was born and I will give you a complete account of the system, and expound the actual teachings of the great explorer of the truth, the master-builder of human happiness. No one rejects, dislikes, or avoids pleasure itself, because it is pleasure, but because those who do not know how to pursue pleasure rationally encounter consequences that are extremely painful. Nor again is there anyone who loves or pursues or desires to obtain pain of itself, because it is pain, but because occasionally circumstances occur in which toil and pain can procure him some great pleasure. To take a trivial example, which of us ever undertakes laborious physical exercise, except to obtain some advantage from it? But who has any right to find fault with a man who chooses to enjoy a pleasure that has no annoying consequences, or one who avoids a pain that produces no resultant pleasure?',
          imgsrc: require('@/assets/logo.png')
        }, {
          title: 'XXX1 Archive',
          created: '2018-07-07',
          abstract: 'At vero eos et accusamus et iusto odio dignissimos ducimus qui blanditiis praesentium voluptatum deleniti atque corrupti quos dolores et quas molestias excepturi sint occaecati cupiditate non provident, similique sunt in culpa qui officia deserunt mollitia animi, id est laborum et dolorum fuga. Et harum quidem rerum facilis est et expedita distinctio. Nam libero tempore, cum soluta nobis est eligendi optio cumque nihil impedit quo minus id quod maxime placeat facere possimus, omnis voluptas assumenda est, omnis dolor repellendus. Temporibus autem quibusdam et aut officiis debitis aut rerum necessitatibus saepe eveniet ut et voluptates repudiandae sint et molestiae non recusandae. Itaque earum rerum hic tenetur a sapiente delectus, ut aut reiciendis voluptatibus maiores alias consequatur aut perferendis doloribus asperiores repellat.',
          imgsrc: require('@/assets/mountain.jpg')
        }, {
          title: 'XXX2 Archive',
          created: '2018-07-08',
          abstract: 'On the other hand, we denounce with righteous indignation and dislike men who are so beguiled and demoralized by the charms of pleasure of the moment, so blinded by desire, that they cannot foresee the pain and trouble that are bound to ensue; and equal blame belongs to those who fail in their duty through weakness of will, which is the same as saying through shrinking from toil and pain. These cases are perfectly simple and easy to distinguish. In a free hour, when our power of choice is untrammelled and when nothing prevents our being able to do what we like best, every pleasure is to be welcomed and every pain avoided. But in certain circumstances and owing to the claims of duty or the obligations of business it will frequently occur that pleasures have to be repudiated and annoyances accepted. The wise man therefore always holds in these matters to this principle of selection: he rejects pleasures to secure other greater pleasures, or else he endures pains to avoid worse pains.',
          imgsrc: require('@/assets/logo.png')
        }
      ],
      active: 0,
      accountInfo: [],
      files: {},
      imgsrcs: {},
      curTitle: '',
      curAbstract: '',
      curPreviewSrc: '',
      searchKeyWord: '',
      searchResults: []
    }
  },
  created () {
    // script-for sticky-nav
    $(document).ready(function () {
      var navoffeset = $('.header-main').offset().top
      $(window).scroll(function () {
      var scrollpos = $(window).scrollTop()
      if (scrollpos >= navoffeset) {
        $('.header-main').addClass('fixed')
      } else {
        $('.header-main').removeClass('fixed')
      }
      })
    })

    // BOX BUTTON SHOW AND CLOSE
    $('.small-graph-box').hover(function () {
      $(this).find('.box-button').fadeIn('fast')
    }, function () {
      $(this).find('.box-button').fadeOut('fast')
    })
    $('.small-graph-box .box-close').click(function () {
      $(this).closest('.small-graph-box').fadeOut(200)
      return false
    })

    // init navigations
    this.$i18n.locale = localStorage.getItem('locale')
    this.items.push({
      name: this.$t('dashboard.myarchive'),
      icon: 'fa fa-envelope'
    })
    this.items.push({
      name: this.$t('dashboard.mytoken'),
      icon: 'fa fa-tachometer'
    })
    this.items.push({
      name: this.$t('dashboard.uploadfile'),
      icon: 'fa fa-picture-o'
    })
    this.items.push({
      name: this.$t('dashboard.searchresult'),
      icon: 'fa fa-search'
    })

    // init account info
    this.accountInfo.push({
      name: this.$t('mytoken.address'),
      value: '0x123456',
      icon: 'glyphicon glyphicon-credit-card',
      background: '#ff4a43'
    })
    this.accountInfo.push({
      name: this.$t('mytoken.type'),
      value: 'ART',
      icon: 'glyphicon glyphicon-bitcoin',
      background: '#22beef'
    })
    this.accountInfo.push({
      name: this.$t('mytoken.amount'),
      value: '0x2',
      icon: 'glyphicon glyphicon-usd',
      background: '#a2d200'
    })
    this.accountInfo.push({
      name: this.$t('mytoken.rate'),
      value: '0.001',
      icon: 'glyphicon glyphicon-piggy-bank',
      background: '#8e44ad'
    })
  },
  methods: {
    toggleSidebar: function () {
      if (this.toggle) {
        $('.page-container').addClass('sidebar-collapsed').removeClass('sidebar-collapsed-back')
        $('#menu span').css({'position': 'absolute'})
      } else {
        $('.page-container').removeClass('sidebar-collapsed').addClass('sidebar-collapsed-back')
        setTimeout(function () {
          $('#menu span').css({'position': 'relative'})
        }, 400)
      }
      this.toggle = !this.toggle
    },
    changeTab: function (idx) {
      if (this.active === idx) {
        return
      }
      this.active = idx
    },
    selectFile: function (e, idx) {
      // console.log(e)
      // console.log(e.target.value)
      var fr = new FileReader()
      fr.onload = function () {
        $('#inputPreview').attr('src', fr.result)
      }
      fr.readAsDataURL(e.target.files[0])
    },
    previewArchive: function (idx) {
      this.curTitle = this.archives[idx].title
      var limitCharactor = 800
      this.curAbstract = this.archives[idx].abstract.length > limitCharactor ? this.archives[idx].abstract.substring(0, limitCharactor) + '...' : this.archives[idx].abstract
      this.curPreviewSrc = this.archives[idx].imgsrc
    },
    search: function () {
      var tempSearchKeyword = $('#keyword').val()
      tempSearchKeyword = tempSearchKeyword.replace(/(^\s*)|(\s*$)/g, '')
      if (this.isEmpty(tempSearchKeyword)) {
        // give warnings here
        console.log("No input...")
      }
      if (this.searchKeyWord === tempSearchKeyword) {
        this.active = 3
        return
      }
      this.searchKeyWord = tempSearchKeyword
      console.log('search result: ' + this.searchKeyWord)
      // here request server to do search task
      this.searchResults.splice(0, this.searchResults.length) // clear search result
      this.searchResults.push({
        title: 'XXX1 Archive',
        created: '2018-07-07',
        abstract: 'At vero eos et accusamus et iusto odio dignissimos ducimus qui blanditiis praesentium voluptatum deleniti atque corrupti quos dolores et quas molestias excepturi sint occaecati cupiditate non provident, similique sunt in culpa qui officia deserunt mollitia animi, id est laborum et dolorum fuga. Et harum quidem rerum facilis est et expedita distinctio. Nam libero tempore, cum soluta nobis est eligendi optio cumque nihil impedit quo minus id quod maxime placeat facere possimus, omnis voluptas assumenda est, omnis dolor repellendus. Temporibus autem quibusdam et aut officiis debitis aut rerum necessitatibus saepe eveniet ut et voluptates repudiandae sint et molestiae non recusandae. Itaque earum rerum hic tenetur a sapiente delectus, ut aut reiciendis voluptatibus maiores alias consequatur aut perferendis doloribus asperiores repellat.',
        imgsrc: require('@/assets/mountain.jpg')
      })
      this.searchResults.push({
        title: 'XXX2 Archive',
        created: '2018-07-08',
        abstract: 'On the other hand, we denounce with righteous indignation and dislike men who are so beguiled and demoralized by the charms of pleasure of the moment, so blinded by desire, that they cannot foresee the pain and trouble that are bound to ensue; and equal blame belongs to those who fail in their duty through weakness of will, which is the same as saying through shrinking from toil and pain. These cases are perfectly simple and easy to distinguish. In a free hour, when our power of choice is untrammelled and when nothing prevents our being able to do what we like best, every pleasure is to be welcomed and every pain avoided. But in certain circumstances and owing to the claims of duty or the obligations of business it will frequently occur that pleasures have to be repudiated and annoyances accepted. The wise man therefore always holds in these matters to this principle of selection: he rejects pleasures to secure other greater pleasures, or else he endures pains to avoid worse pains.',
        imgsrc: require('@/assets/logo.png')
      })
      this.active = 3
    },
    logout: function () {
      // do logout work here
      this.$router.push('/')
    },
    testAPI: function () {
      this.$http.get('/bing').then(response => {
        // get body data
        this.someData = response.body;
        console.log(this.someData)
      }, response => {
        // error callback
      })
      this.$http.get('/baidu').then(function (response) {
        console.log(response)
      })
    },
    isEmpty: function (str) {
      if (typeof str == 'undefined' || str == null || str.lenght < 1 || str == '') {
        return true
      }
      return false
    }
  }
}
</script>



